<template>
    <div class="top-root">
        <div class="top-con">
        <el-button-group>
            <el-autocomplete
                popper-class="marker-autocomplete"
                custom-item="marker-autocomplete"
                v-model="searchVal"
                :fetch-suggestions="querySearchAsync"
                placeholder="请输入查询点"
                @select="handleSelect"
                icon="search"
                class="top-autocomplete"
                >
            </el-autocomplete>
            <el-button type="primary" icon="arrow-down" @click="openSetting"></el-button>
        </el-button-group>

        </div>
        <!--setting dialog-->
        <el-dialog
            title="地图设置"
            :visible.sync="settingVisible"
            size="tiny"
            custom-class="setting-dialog map-dialog slideInDown"
            :modal=false
            @open="onOpenSetting"
            :before-close="handleClose">
            <el-tabs v-model="activeName" @tab-click="handleClick">
                <!-- <el-tab-pane label="用户管理" name="first">用户管理</el-tab-pane> -->
                <el-tab-pane label="配置管理" name="mapSetting">
                    <div class="map-setting">
                        <label>动态刷新：</label>
                        <el-switch
                            v-model="isUpdateTrail"
                            @change="changeUpdateTrail"
                            on-text="开启"
                            off-text="关闭">
                        </el-switch>
                    </div>

                </el-tab-pane>
                <el-tab-pane label="角色管理" name="userSetting">
                    <div class="map-setting">
                        <ul>
                            <li style="margin-bottom: 5px;">
                                <label style="width: 100px; display:inline-block;">添加好友：</label>
                                <el-button type="primary" icon="plus" @click="openUserlist" class="add-user"></el-button>
                            </li>
                            <li>
                                <label style="width: 100px; display:inline-block;">登出：</label>
                                <el-button type="primary" icon="circle-close" @click="logOut" class="add-user"></el-button>
                            </li>
                        </ul>

                    </div>
                </el-tab-pane>
            </el-tabs>
            <span slot="footer" class="dialog-footer">
            </span>
        </el-dialog>
        <!--userlist dialog-->
        <el-dialog
            title="用户列表"
            :visible.sync="userListVisible"
            size="large"
            custom-class="login-dialog map-dialog slideInDown"
            :modal=true
            @open="onOpenSetting" 
        >
            <div class="userlist">
                <ul>
                    <li v-for="user of userList"> 
                        <span class="user-avatar"></span>
                        <span class="user-name">{{user.username}}</span>
                        <el-button type="primary" style="float: right" v-if="user.isNotFriend" @click="addUser(user)">添加好友</el-button>
                        <el-button type="danger" style="float: right" v-else  @click="unAddUser(user)">取消好友</el-button>
                    </li>
                </ul>
            </div>
        </el-dialog>
        <!--login dialog-->
        <el-dialog
            :title=loginTitle
            :visible.sync="loginDialogVisible"
            size="large"
            custom-class="login-dialog map-dialog slideInDown"
            :modal=true
            @open="onOpenSetting"
            :show-close=false
            :before-close="handleLoginClose">
            <login 
                @loginSuccess="loginSuccessCallback"
                v-if="loginComp == 'login'"
                @forgetPass="forgetPassCb"
                @registerAccount="registerAccountCb"
            >
            </login>
            <register
            v-else
            @registerSuccess="registerSuccessCallback"
            ></register>
        </el-dialog>
    </div>

</template>
<script>
import register from './register'
import login from './login'
import Vue from 'vue'
import {mapState} from 'vuex'
import _ from 'lodash'
import utils from '../helper/utils'
import configUrl from './apiConfig'
import { MessageBox } from 'mint-ui';

Vue.component('marker-autocomplete', {
    functional: true,
    render: function (h, ctx) {
        const item = ctx.props.item;
        return h('li', ctx.data, [
            h('div', { 
                attrs: { 
                    class: 'name' 
                } 
            }, [item.name]),
            h('span', { 
                attrs: { 
                    class: 'label' 
                } 
            }, [item.label])
        ]);
    },
    props: {
        item: { 
            type: Object, 
            required: true 
        }
    }
});

export default {
    data() {
        return {
            settingVisible: false,
            loginDialogVisible: false,
            userListVisible: false,
            searchVal: '',
            isUpdateTrail: true,
            activeName: 'mapSetting',
            loginComp: 'login',
            loginTitle: '登录',
            friendList: [],
            userList: [
                {username: 'test1'},
                {username: 'test2'}
            ],
            markerList: []
        }
    },
    components: {
        'login': login,
        'register': register
    },
  methods: {
    handleSelect(selectVal) {
        const me = this
        //find which marker will be hightlight
        const resultMarker =  _.find(this.markerList, (marker) => {
            let extData = marker.getExtData();
            return extData.id == selectVal.id
        })
        me.highLightMarker(resultMarker)
    },
    handleClick() {
        console.log(this.activeName)
    },
    openSetting() {
        this.settingVisible = true
    },
    onOpenSetting() {
        const mapDialog = document.querySelector('.setting-dialog')
        const parentNode = document.querySelector('.top-root > .el-dialog__wrapper')
        utils.createCanvasBg(mapDialog, parentNode, 'imageMask')
    },

    highLightMarker(marker) {
        //get marker object
        //define marker's behavior
        marker.setAnimation('AMAP_ANIMATION_BOUNCE')
        setTimeout(_ => {
            marker.setAnimation('AMAP_ANIMATION_NONE')
        }, 3000)
        console.log(this)
        this.$parent.mapHelper.setMapZoomAndCenter(undefined, marker.getPosition())
    },
    handleClose(done) {
        done()
    },
    querySearchAsync(queryString, cb) {
        const me = this;
        //this.$store.dispatch('changeModalStatus')
        let searchCollection = this.loadSearchCollection();
        let results = queryString ? searchCollection.filter(this.createStateFilter(queryString)) : searchCollection;
        console.log(results)
        clearTimeout(this.timeout);
        this.timeout = setTimeout(_ => {
            cb(results);
        }, 500 * Math.random());
    },
    createStateFilter(queryString) {
        return (state) => {
            return (state.name.indexOf(queryString.toLowerCase()) === 0)
        };
    },
    loadSearchCollection() {
        let extDataArr = []
        _.each(this.markerList, (marker) => {
            let extData = marker.getExtData();
            extDataArr.push({
                name: extData.username,
                id:  extData.id
            })
        })
        return extDataArr
    },
    showLogin() {
        this.loginDialogVisible = true
    },
    loginSuccessCallback() {
        let me = this;
        this.loginDialogVisible = false
        this.$emit('loginSuccess')
        console.log(1)
        this.getfriends((userList) => {
            me.$emit('onGetUserlist', userList)
        });
        //
        if (markerRefreshTimer) {
            clearInterval(markerRefreshTimer)
        }
        //need refresh the marker pos
        let markerRefreshTimer = setInterval(() => {
            this.getfriends((userList) => {
                me.$emit('onGetUserlist', userList)
            });
        }, 20000)
        //need refresh the message 
        let messageRefreshTimer = setInterval(() => {
            this.getNewMessage(function() {

            })
        }, 20000)
    },
    changeFriendStatus(userList) {
        let me = this;
        if (!this.friendList.length) {
            this.friendList = JSON.parse(utils.getLocalStorage('friendList'))
        }
        _.each(userList, (user) => {
            let friend = _.find(me.friendList, {id: user.id})
            if (friend) {
                user.isNotFriend = false;
            } else {
                user.isNotFriend = true;
            }
        })
        console.log(userList)
    },
    registerSuccessCallback() {
        this.loginComp = 'login'
    },
    forgetPassCb() {
        
    },
    registerAccountCb() {
        this.loginTitle = '注册'
        this.loginComp = 'register'
    },
    handleLoginClose(done) {
        if (utils.getLocalStorage('login')) {
            done()
        }
    },
    openUserlist() {
        this.getUserList((userList) => {
            this.changeFriendStatus(userList)
            this.userListVisible = true
        });
    },
    getUserList(cb) {
        let me = this
        this.$http.get(configUrl.user + '/all')
            .then(res => {
                let data = res.data
                console.log(res)
                if (data.status == 200) {
                    let userList = JSON.parse(data.msg).userId
                    me.userList = userList
                    _.remove(userList, (user) => {
                        let currentUserId = utils.getLocalStorage('userId')
                        return user.id == currentUserId
                    })
                    cb && cb(userList)
                    //need refresh the marker
                    //need get the message num
                } else {
                    me.logOut()
                }
            }).catch(err => {
                console.log(err)
            })
    },
    getfriends(cb) {
        let me = this;
        let friendList = [];
        let currentUserId = utils.getLocalStorage('userId')
        this.$http.get(configUrl.user + `/friends/${currentUserId}`)
            .then((res) => {
                let data = res.data
                if (data.status == 200) {
                    friendList = JSON.parse(data.msg).friends
                    utils.setLocalStorage('friendList', JSON.stringify(friendList))
                    me.friendList = friendList
                    cb && cb(friendList)
                } else {
                    me.logOut()
                }
            })
            .catch(err => {
                console.log(err)
            })
    },

    getNewMessage(cb) {
        //get friend user list
        let me = this
        let friendList = [];
        let currentUserId = utils.getLocalStorage('userId')
        this.$http.get(configUrl.user + `/friends/${currentUserId}`)
            .then((res) => {
                let data = res.data
                if (data.status == 200) {
                    friendList = JSON.parse(data.msg).friends
                    //get the new message
                    me.friendList = friendList
                    _.each(friendList, (val) => {
                        let toUserId = val.id
                        this.$http.get(configUrl.messages + `/0/${currentUserId}/${toUserId}`).then(res => {
                            let data = res.data
                            if (data.status == 200) {
                                let msg = JSON.parse(data.msg)
                                if (msg.length) {
                                    _.orderBy(msg, ['cTimestamp'], ['desc']);
                                    let num = _.find(msg, (item) => {
                                        return item.status == 1 && item.fromId != currentUserId
                                    })
                                    let msgNum = num ? num.length : 1
                                    if (msg[0].status == 1 && msg[0].fromId != currentUserId) {
                                        //need refresh the user avatar
                                        me.$emit('refreshUserMarker', toUserId, msg[0], msgNum)
                                    }
                                }
                            }
                        }).catch(err => {
                            console.log(err)
                        })
                    })
                }
            })
            .catch((err) => {
                console.log(err)
            })
        cb && cb()
    },

    addUser(user) {
        let me = this;
        //category = 1 means only see
        //category = 2 means chat and see
        let category = 1;
        let currentUserId = utils.getLocalStorage('userId')
        this.$http.post(configUrl.userGroup, {
          category: category,
          fromId: currentUserId,
          toId: user.id
        })
        .then(res => {
            let data = res.data
            if (data.status == 200) {
                MessageBox('添加好友成功', data.msg);
            } else {
                MessageBox('添加好友失败', data.msg);
            }
        }).catch(err => {
            console.log(err)
        })
    },

    unAddUser(user) {
        let me = this;
        //category = 1 means only see
        //category = 2 means chat and see
        let category = 1;
        let currentUserId = utils.getLocalStorage('userId')
        this.$http.post(configUrl.userGroup + '/unbind', {
          fromId: currentUserId,
          toId: user.id
        })
        .then(res => {
            let data = res.data
            if (data.status == 200) {
                MessageBox('取消好友成功', data.msg);
            } else {
                MessageBox('取消好友失败', data.msg);
            }
        }).catch(err => {
            console.log(err)
        })    
    },

    changeUpdateTrail() {
        if(this.isUpdateTrail) {
            MessageBox('动态轨迹刷新已开启');
        } else {
            MessageBox('动态轨迹刷新已关闭');
        }
        this.$emit('changeUpdateTrail', this.isUpdateTrail)
    },

    logOut() {
        utils.clearLocalStorage('token', '');
        utils.clearLocalStorage('userId', '');
        this.loginDialogVisible = true
        this.settingVisible = false
    }
  }
}
</script>
<style lang = 'scss'>
body {
    .marker-autocomplete {
        li {
            line-height: normal;
            padding: 7px;

            .name {
            text-overflow: ellipsis;
            overflow: hidden;
            }
            .label {
            font-size: 12px;
            color: #b4b4b4;
            }

            .highlighted .addr {
            color: #ddd;
            }
        }
    }


    .top-con {
        width: 100%;
        position: absolute;
        top: 10px;
        z-index: 200;
        text-align: right;
    }
    .top-autocomplete {
        width: 85%;
        float: left;
    }
    .el-button-group {
        width: 100%;
        padding-left: 20px;
    }

    .map-setting {
        width: 100%;
        height: 100%;
        text-align: left;
    }
    .userlist {
        
        li {
            margin: 5px;
            text-align: left;
            padding-bottom: 5px;
            border-bottom: 1px solid #48576a;
        }
        .user-avatar{
            width: 40px;
            height: 40px;
            border-radius: 50%;
            background: #fff;
            vertical-align: top;
            display: inline-block;
        }
        .user-name{
            display: inline-block;
            height: 40px;
            line-height: 40px;
            font-size: 20px;
            color: #fff;
            margin-left: 5px;
        }
    }
}

</style>


